<template>
  <div class="devGroupManage">
		<div class="nav">
			<!-- <Input v-model="sendData.identifiers" placeholder="请输入imei" style="width: 300px" />
			<Input v-model="sendData.taskName" placeholder="任务名称" style="width: 300px;margin-left: 1.25rem;" />
			<Button type="primary" icon="ios-search" @click="search" style="margin-left: 10px;">查询</Button> -->
		</div>
		<div class="content">
			<div class="box">
				<el-table
				  align='center'
				  :data="tableData"
				  tooltip-effect="dark"
				  style="width:	100%;"
					height="100%"
					@expand-change='lookCount'
				  ref="multipleTable">
					<el-table-column type="expand">
						<template slot-scope="props">
							<el-form label-position="left" inline class="demo-table-expand">
								<el-divider content-position="left">任务状态总汇</el-divider>
								<el-form-item :label="item.status + ':'" v-for="(item,index) in taskStatusList">
									<span>{{item.count}}</span>
								</el-form-item>
								<el-divider content-position="left">任务状态详情</el-divider>
								<el-form-item :label="item.sys||item.msg + ':'" v-for="(item,index) in taskStatusInfoList">
									<span>{{item.cnt}}</span>
								</el-form-item>
							</el-form>
						</template>
					</el-table-column>
				  <el-table-column
				    prop="name"
				    sortable
				    label="任务名称"
				    width="250">
				  </el-table-column>
					<el-table-column
					  prop="tid"
					  sortable
					  label="tid"
					  width="200">
					</el-table-column>
					<el-table-column
					  prop="iid"
					  sortable
					  label="iid"
					  width="200">
					</el-table-column>
					<el-table-column
					  label="任务创建时间"
					  sortable
					  min-width="200">
						<template slot-scope="scope">
						  <span>{{scope.row.iid|timeHendel}}</span>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<div class="page-box">
				<el-pagination
					@size-change="rowsChange"
					@current-change="pageChange"
					:current-page="sendData.page+1"
					:page-sizes="[100,500]"
					:page-size="sendData.rows"
					layout="total, sizes, prev, pager, next, jumper"
					:total="totalRows">
				</el-pagination>
			</div>
		</div>
  </div>
</template>

<script>
import {deviceGroups,executionRecordFormList,executionRecordFormListGroupBy,executionRecordFormListGroupByFailure} from '@/api/index.js'

export default{
	name:'executionRecordForm',
	data(){
		return{
			tableData:[],
			taskStatusList:[],
			taskStatusInfoList:[],
			totalRows:0,
			sendData:{
				identifiers:'',
				groupName:'',
				taskName:"",
				page:0,
				rows:100
			}
		}
	},
	created() {
		this.getviewTaskLogs()
	},
	methods:{
		lookCount(row,e){
			if(e.length > 0){
				executionRecordFormListGroupBy(row).then(res => {
					this.taskStatusList = res.content
				})
				executionRecordFormListGroupByFailure(row).then(res => {
						this.taskStatusInfoList = res.content
				})
			}
		},
		search(){
			this.sendData.page = 0
			this.getviewTaskLogs()
		},
		pageChange(page){
			this.sendData.page = page-1
			this.getviewTaskLogs()
		},
		rowsChange(rows){
			this.sendData.rows = rows
			this.getviewTaskLogs()
		},
		getviewTaskLogs(){
			executionRecordFormList(this.sendData).then(res => {
				this.tableData = res.content.content
				this.totalRows = res.content.totalElements
			})
		}
	},
	filters: {
	  timeHendel(val) {
	    if (val === null) {
	      return "";
	    }
	    val = Number(val + "");
	    var date = new Date(val*1000); // 时间戳为10位需*1000，时间戳为13位的话不需乘1000
	    // console.log('触发了时间格式方法'+ val)
	    var Y = date.getFullYear() + "-";
	    var M =
	      (date.getMonth() + 1 < 10
	        ? "0" + (date.getMonth() + 1)
	        : date.getMonth() + 1) + "-";
	    var D =
	      (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + " ";
	    var h =
	      (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
	    var m =
	      (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
	      ":";
	    var s =
	      date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
	    // console.log('触发了时间格式方法'+ Y + M + D + h + m + s)
	    return Y + M + D + h + m + s;
	  },
	},
}
</script>

<style scoped lang="scss">
	.devGroupManage{
		position: relative;
		width: 100%;
		height: 100%;
		padding: 20px;
		box-sizing: border-box;
		.nav{
			position: relative;
			width: 100%;
			height: 64px;
			padding: 15px 20px;
			box-sizing: border-box;
			background: #FFFFFF;
			border-radius: 4px;
		}
		.content{
			position: relative;
			width: 100%;
			height: calc(100% - 84px);
			margin-top: 20px;
			padding: 15px;
			box-sizing: border-box;
			background: #fff;
			border-radius: 4px;
			.box{
				position: relative;
				width: 100%;
				height: calc(100% - 60px);
				.demo-table-expand {
				    font-size: 0;
				  }
				  .demo-table-expand label {
				    width: 90px;
				    color: #ccc;
				  }
				  .demo-table-expand .el-form-item {
				    margin-right: 0;
				    margin-bottom: 0;
				    width: 100%;
				  }
			}
			.page-box{
				position: relative;
				width: 100%;
				height: 70px;
				display: flex;
				align-items: center;
				justify-content: flex-end;
			}
		}
	}
</style>
